html, * {
  width: 100%;
  box-sizing: border-box;
}

.App {
  position: relative;
  color: #ffffff;
}

.App:before {
  content: '';
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #0f172a;
  z-index: -1;
  color: #fff;
}

header, main {
  padding: 30px;
}

.profile-img img {
  width: 100%;
  height: auto;
}

.profile-img {
  max-width: 200px;
  height: 200px;
  margin: 0 auto;
  background: #ffffff3d;
  border-radius: 50%;
  overflow: hidden;
  position: relative;
}

.profile h1 {
  margin-bottom: 0;
  font-size: 2.25rem;
  font-weight: 600;
  letter-spacing: -1px;
  line-height: 2.5rem;
}

h2 {
  font-size: 1.125rem;
  line-height: 1.75rem;
  margin: 0;
  font-weight: 400;
}

p {
  line-height: 1.5;
  max-width: 30rem;
  color: #ffffff88;
}

p a {
  color: #fff;
  text-decoration: none;
}

.profile h2, .profile p {
  margin-top: .75rem;
}

.social-links {
  list-style: none;
  display: flex;
  gap: 15px;
  padding: 0;
}

.social-links li {width: fit-content;}

.social-links svg {
  width: 30px;
  fill: #ffffff88;
}

.social-links a:hover svg {fill: #ffffff;}

main > div:not(:first-child) {margin-top: 70px;}

.project-card .thumbnail {
  max-width: 230px;
  aspect-ratio: 10/5;
  background: #fafafa;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
  margin-bottom: 10px;
}

.project-card h2 a {
  color: #fff;
  text-decoration: none;
}

.project-card .thumbnail img {
  width: 80%;
}

.experiences a {
  color: #fff;
  text-decoration: none;
}

h3 {text-transform: uppercase;}

.experiences h3 {margin-bottom: 20px;}

.position-company {
  margin-bottom: 0;
  margin-top: 0;
}

.timeline {
  font-weight: 400;
  margin-bottom: 0px;
  color: #ffffff88;
}

em {color: #ffffff;}

p a:after, h2 a:after, h3 a:after, h4 a:after, h5 a:after, h6 a:after {
  content: '';
  height: 10px;
  width: 10px;
  display: inline-block;
  color: #fff;
  background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' fill='%23ffffff88' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 512 512'%3E%3Cpath d='M352 0c-12.9 0-24.6 7.8-29.6 19.8s-2.2 25.7 6.9 34.9L370.7 96 201.4 265.4c-12.5 12.5-12.5 32.8 0 45.3s32.8 12.5 45.3 0L416 141.3l41.4 41.4c9.2 9.2 22.9 11.9 34.9 6.9s19.8-16.6 19.8-29.6V32c0-17.7-14.3-32-32-32H352zM80 32C35.8 32 0 67.8 0 112V432c0 44.2 35.8 80 80 80H400c44.2 0 80-35.8 80-80V320c0-17.7-14.3-32-32-32s-32 14.3-32 32V432c0 8.8-7.2 16-16 16H80c-8.8 0-16-7.2-16-16V112c0-8.8 7.2-16 16-16H192c17.7 0 32-14.3 32-32s-14.3-32-32-32H80z'/%3E%3C/svg%3E");
  background-size: contain;
  margin: 0 5px;
  background-repeat: no-repeat;
}

p a:hover:after, h2 a:hover:after, h3 a:hover:after, h4 a:hover:after, h5 a:hover:after, h6 a:after {background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' fill='%23ffffff' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 512 512'%3E%3Cpath d='M352 0c-12.9 0-24.6 7.8-29.6 19.8s-2.2 25.7 6.9 34.9L370.7 96 201.4 265.4c-12.5 12.5-12.5 32.8 0 45.3s32.8 12.5 45.3 0L416 141.3l41.4 41.4c9.2 9.2 22.9 11.9 34.9 6.9s19.8-16.6 19.8-29.6V32c0-17.7-14.3-32-32-32H352zM80 32C35.8 32 0 67.8 0 112V432c0 44.2 35.8 80 80 80H400c44.2 0 80-35.8 80-80V320c0-17.7-14.3-32-32-32s-32 14.3-32 32V432c0 8.8-7.2 16-16 16H80c-8.8 0-16-7.2-16-16V112c0-8.8 7.2-16 16-16H192c17.7 0 32-14.3 32-32s-14.3-32-32-32H80z'/%3E%3C/svg%3E");}

.project-card, .experience-card {
  transition: all 200ms ease;
  width: fit-content;
  padding: 10px;
  border-radius: 10px;
  border: 1px solid transparent;
}

.project-card:not(:first-child), .experience-card:not(:first-child) {margin-top: 30px;}

.project-card:hover, .experience-card:hover {
  background: rgba( 255, 255, 255, 0.05 );
  /*box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );*/
  backdrop-filter: blur( 0px );
  -webkit-backdrop-filter: blur( 0px );
  border-color: rgba( 255, 255, 255, 0.18 );
}

.contact h4 {margin-bottom: 0;}

.email {
  color: #ffffff;
  text-decoration: none;
}

.email:before {
  content: '';
  height: 10px;
  width: 10px;
  display: inline-block;
  color: #fff;
  background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' fill='%23ffffff88' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 512 512'%3E%3Cpath d='M48 64C21.5 64 0 85.5 0 112c0 15.1 7.1 29.3 19.2 38.4L236.8 313.6c11.4 8.5 27 8.5 38.4 0L492.8 150.4c12.1-9.1 19.2-23.3 19.2-38.4c0-26.5-21.5-48-48-48H48zM0 176V384c0 35.3 28.7 64 64 64H448c35.3 0 64-28.7 64-64V176L294.4 339.2c-22.8 17.1-54 17.1-76.8 0L0 176z'/%3E%3C/svg%3E");
  background-size: contain;
  margin: 0 5px 0 0;
  background-repeat: no-repeat;
}

.email:hover:before {background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' fill='%23ffffff' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 512 512'%3E%3Cpath d='M48 64C21.5 64 0 85.5 0 112c0 15.1 7.1 29.3 19.2 38.4L236.8 313.6c11.4 8.5 27 8.5 38.4 0L492.8 150.4c12.1-9.1 19.2-23.3 19.2-38.4c0-26.5-21.5-48-48-48H48zM0 176V384c0 35.3 28.7 64 64 64H448c35.3 0 64-28.7 64-64V176L294.4 339.2c-22.8 17.1-54 17.1-76.8 0L0 176z'/%3E%3C/svg%3E");}

@media (min-width: 640px) {
  .profile-img {margin: 0;}
  h2 {font-size: 1.25rem;}
}

@media (min-width: 768px) {
  .profile h1 {
    font-size: 3rem;
    line-height: 1;
  }

  header, main {padding: 60px;}
}

@media (min-width: 1024px) {
  .App {
    display: flex;
    align-items: flex-start;
  }

  header {
    position: fixed;
    height: 100vh;
    width: 50%;
    padding-right: 0;
    padding-bottom: 10px;
    display: flex;
    flex-direction: column;
  }

  main {
    width: 50%;
    margin-left: auto;
  }

  .profile {flex-grow: 1;}
}
